import {
    AppBar,
    Button,
    Container,
    createTheme, Dialog, DialogActions, DialogContent, DialogTitle,
    Divider, IconButton,
    makeStyles, MenuItem,
    Paper, Select, TextField,
    ThemeProvider,
    Typography
} from "@material-ui/core";
import {DataGrid} from "@mui/x-data-grid";
import {useState} from "react";
import {useFormik} from "formik";
import ClearIcon from '@material-ui/icons/Clear';

const useStyles = makeStyles(theme => ({
    paper: {
        padding: '20px'
    },
    manage: {
        display: 'flex',
        flexDirection: 'column'
    },
    manageButtons: {
        display: 'flex',
        justifyContent: 'flex-end',
        padding: '20px',
        '& > button': {
            marginLeft: '15px'
        }
    },
    country: {
        display: 'flex',
        marginRight: '40px'
    },
    date: {
        display: 'flex',
        alignItems: 'center'
    },
    header: {
        display: 'flex',
        alignItems: 'center',
        marginBottom: '10px'
    },
    select: {
        marginLeft: '20px'
    },
    add: {
        margin: '20px 0',
        display: 'flex',
        alignItems: 'center',
        '& > button': {
            marginRight: '15px'
        }
    },
    form: {
        display: 'flex',
        flexDirection: 'column'
    },
    field: {
        marginBottom: '10px'
    },
    dialogHeader: {
        display: 'flex',
        justifyContent: 'space-between',
        '& > button': {
            marginRight: '15px'
        },
        alignItems: 'center'
    }
}))



const Manage = ({ packages, addPackage, deletePackage }) => {

    const columns = [
        { field: 'track', headerName: '-', width: 180 },
        { field: 'fio', headerName: '', width: 250 },
        { field: 'weight', headerName: '', width: 150 },
        { field: 'inside', headerName: '', width: 200 },
        { field: 'price', headerName: '', width: 200 },
        { field: 'city', headerName: '', width: 200 },
        { field: 'date', headerName: ' ', width: 230 },
        { field: 'actions', headerName: '', width: 230, renderCell: (params) => <div><Button onClick={ () => deletePackage(params.row.id) }></Button><Button></Button></div> }
    ];

    const [createPackage, setCreatePackage] = useState(false)

    const handleOpenAddPackage = () => {
        setCreatePackage(true)
    }

    const handleCloseAddPackage = () => {
        setCreatePackage(false)
    }

    const styles = useStyles();
    return (
        <div className={styles.manage}>
            <div className={styles.manageButtons}>
                <Button variant={'contained'} color={'primary'}>  </Button>
                <Button variant={'contained'} color={'primary'}> </Button>
                <Button>-  </Button>
            </div>
            <Paper className={styles.paper} elevation={0}>
                <div className={styles.header}>
                    <div className={styles.country}>
                        <Typography> </Typography>
                        <Select className={styles.select} value={0}>
                            <MenuItem value={0}></MenuItem>
                            <MenuItem value={1}></MenuItem>
                        </Select>
                    </div>
                    <div className={styles.date}>
                        <Typography> </Typography>
                        <Select className={styles.select} value={0}>
                            <MenuItem value={0}>11.11.2022</MenuItem>
                        </Select>
                    </div>
                </div>
                <Divider/>
                <div className={styles.add}>
                    <Button onClick={handleOpenAddPackage} variant={'outlined'}> </Button>
                    <Button variant={'outlined'}> </Button>
                </div>
                <DataGrid autoHeight columns={columns} rows={packages}/>
            </Paper>
            <AddPackage id={packages.length + 1} addPackage={addPackage} open={createPackage} handleClose={handleCloseAddPackage}/>
        </div>

    )
}

const AddPackage = ({open, handleClose, addPackage, id}) => {

    const styles = useStyles();
    const form = useFormik({
        initialValues: {
            track: '',
            fio: '',
            weight: '',
            inside: '',
            price: '',
            city: '',
            date: '',
        },
        onSubmit: (values) => {
            addPackage({...values, id: id})
            form.resetForm();
        }
    });

    const getLabel = (key) => {
        switch (key) {
            case 'track':
                return '-'
            case 'fio':
                return ''
            case 'weight':
                return ''
            case 'inside':
                return ''
            case 'price':
                return ''
            case 'city':
                return ''
            case 'date':
                return ''
        }
    }

    const handleConfirm = () => {
        form.handleSubmit();
        handleClose();
    }

    const handleCloseDialog = () => {
        handleClose();
        form.resetForm();
    }

    return (
        <Dialog maxWidth={'sm'} fullWidth open={open} onClose={handleCloseDialog}>
            <div className={styles.dialogHeader}>
                <DialogTitle> </DialogTitle>
                <IconButton onClick={handleCloseDialog}>
                    <ClearIcon/>
                </IconButton>
            </div>
            <DialogContent>
                <div className={styles.form}>
                    {
                        Object.keys(form.initialValues).map(x =>
                            <TextField className={styles.field} variant={'outlined'} fullWidth id={x} name={x} label={getLabel(x)} onChange={form.handleChange} value={form.values[x]}/>
                        )
                    }
                </div>
            </DialogContent>
            <DialogActions>
                <Button onClick={handleCloseDialog}></Button>
                <Button onClick={handleConfirm} variant={'contained'} color={'secondary'}></Button>
            </DialogActions>
        </Dialog>
    )
}


export default Manage;